.form-field {
    margin: 1rem 0;
    position: relative;

    & input {
        height: 2.2rem;
        border: 1px solid $silver;
    }

    & input,
    & textarea {
        width: 100%;
        // padding: 0.5rem;
        margin-bottom: 0.5rem;
        background: $transparent-red;
        border-radius: 4px;
        padding: 3.25em 0.5em 1.2em;
    }

    & input[type=checkbox] {
        height: 20px;
        width: 20px;
        background-color: $transparent-red;
    }
}

.form-field__label {
    display: block;
    font-weight: 700;
    margin-bottom: 0.25rem;
    position: absolute;
    top: 8px;
    left: 8px;
}

.form-field--alt {
    background: rgba(211, 67, 62, 0.1);
    border-radius: 4px;
    padding: 1.2em 0.5em 1.2em;
    border: 1px solid #bfbfbf;
}

.form-field--alt .form-field__label {
    position: initial;
    float: left;
    margin-right: 1rem;
}

.form-field__label--required {
    font-size: 0.75rem;
}

// .form-field__label[for=rsvp] {
